.order-row,
.order-row-2 {
  width: 100%;
  height: 5vh;
  margin: 10px 0px;
  display: grid;
  grid-template-columns: 12% 88%;
  border-top: 1px solid rgb(191, 191, 191);
  border-bottom: 1px solid rgb(191, 191, 191);
  padding: 2px 0px;
  background: white;
}

.order-left-div {
  background: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 15px;
  flex-direction: column;
}

.order-right-div,
.order-right-div-2,
.order-right-div-3 {
  display: grid;
  grid-template-columns: repeat(97, 1fr);
  grid-template-rows: 100%;
  /* grid-gap: 2px; */
  border-radius: 5px;
}

.grid-item {
  background: white;
  border-left: 1px solid rgb(149, 165, 166);
}

.grid-item:hover {
  background: #0099a8;
}

.time-legenda-row {
  width: 100%;
  display: grid;
  grid-template-columns: 12% 88%;
  border-top: 2px solid rgba(239, 239, 240, 1);
  border-bottom: 2px solid rgba(239, 239, 240, 1);
  background: white;
}

